@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&display=swap);
body,
h1 {
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
}

h1 {
  padding: 2vh;
}

.App {
  height: 100vh;
  background-color: rgba(74, 221, 160, 0.57);
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.todoForm {
  position: relative;
  left: -7%;
  display: grid;
  grid-template-columns: 1.7fr 7fr 1fr;
  justify-content: center;
  align-items: center;
  width: 85vw;
}

.todoForm__elements {
  display: flex;
  flex-direction: column;
}

.todoForm__element {
  position: relative;
  width: 100%;
  padding: 12px 20px;
  margin: 3px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.todoForm__errors {
  display: grid;
  grid-template-rows: 1fr 1fr;
}

.todoForm__error {
  color: red;
  width: 100%;
  padding: 12px 0;
  margin: 3px 0;
  display: inline-block;
  box-sizing: border-box;
}

button {
  background-color: rgb(51, 172, 51);
  color: white;
  height: 5rem;
  border-radius: 2rem;
  margin-left: 1rem;
}

.todo-list{width:70vw;margin:0 auto;padding:0}.todo-list__item{display:flex;height:10vh;align-items:center;justify-content:space-between;text-align:center;margin-bottom:1vh;padding:2vh;background-color:#fff;border-radius:1em}.todo-list__item span{display:inline-block;width:23%}.todo-list__item-status{width:2em;height:2em;border:gray 0.1em solid;border-radius:10%}.todo-list__item-status--completed{background-image:url(../../static/media/vcsnormal_93488.a3cb7f14.svg);background-size:cover}

